.wrapper {
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 400px;
    width: 300px;
    border: 1px solid black;
}

.form {
    height: 200px;
    width: 200px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.clarification {
    opacity: 0.6;
    font-size: 14px;
}

.error {
    color: red;
}